import axios from 'axios'
import { inject, provide, ref } from 'vue'

const baseUrl = process.env.BASE_URL

const blobToDataURL = (blob) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onerror = reject
    reader.onload = (e) => resolve(reader.result)
    reader.readAsDataURL(blob)
  })
}

const loadBGM = async () => {
  const { data: blob } = await axios.get(`${baseUrl}bensound-memories.mp3`, {
    responseType: 'blob',
    timeout: 60000 * 3
  })
  const dataUrl = await blobToDataURL(blob)
  return dataUrl
}

const loadPhotos = async () => {
  const srcList = (new Array(6)).fill(null)
    .map((_, i) => `${baseUrl}images/photos/${i + 1}.jpg`)
  const dataUrls = await Promise.all(srcList.map(async (src) => {
    const { data: blob } = await axios.get(src, {
      responseType: 'blob',
      timeout: 60000 * 3
    })
    const dataUrl = await blobToDataURL(blob)
    return dataUrl
  }))

  return dataUrls
}

