const loadSprites = async () => {
  return {
    char1: {
      love: {
        stand: await loadChar1LoveStandSprites()
      }
    },
    char2: {
      love: {
        stand: await loadChar2LoveStandSprites()
      },
      smile: {
        stand: await loadChar2SmileStandSprites()
      }
    },
    pet1: {
      stand: await loadPet1StandSprites()
    },
    pet2: {
      stand: await loadPet2StandSprites()
    }
  }
}

export const useResourcesLoading = () => {
  const resources = ref(null)

  const loadResources = async () => {
    resources.value = {
      bgm: await loadBGM(),
      photos: await loadPhotos(),
      sprites: await loadSprites()
    }
  }

  provide('resources', resources)

  return {
    resources,
    loadResources
  }
}

export const useResources = () => {
  return inject('resources') ?? ref(null)
}
