<template>
  <div v-if="resources">
    <component class="animate__animated animate__fadeIn" :is="currentComponent" @next="onNext" @play-bgm="onPlayBGM"></component>
    <audio id="bgm" :src="resources.bgm" loop></audio>
  </div>
  <div v-else class="loader-container">
    <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from 'vue'
import { useResourcesLoading } from '@/utils/load-resources'
import Cover from '@/components/Cover.vue'
import FirstPage from '@/components/FirstPage.vue'
import SecondPage from '@/components/SecondPage.vue'
import ThirdPage from '@/components/ThirdPage.vue'
import FinalPage from '@/components/FinalPage.vue'

export default {
  name: 'App',
  setup () {
    const { resources, loadResources } = useResourcesLoading()

    const steps = [Cover, FirstPage, SecondPage, ThirdPage, FinalPage]
    const step = ref(0)
    const currentComponent = computed(() => steps[step.value])
    const onNext = () => {
      step.value++
    }
    const onPlayBGM = () => {
      document.getElementById('bgm').play()
    }

    onMounted(async () => {
      await loadResources()
    })

    return {
      resources,
      currentComponent,
      onNext,
      onPlayBGM
    }
  }
}
</script>

